<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="mx-auto elevation-2">
        <v-card-text>
          <!--<v-window>
            <v-window-item
              key="card-1"
            >
              <v-card
              color="white"
              elevation="2"
              >
              <v-row
                  class="fill-height"
                  align="center"
                  justify="center"
                >
                  <v-col cols="12">
                    <span class="justify-center error--text">
                      Se debe subir los archivos con mucho cuidado.
                    </span>
                  </v-col>
                </v-row>
              </v-card>
            </v-window-item>
          </v-window>
          <br>-->
          <span class="headline justify-center black--text font-weight-bold">
            Información General
          </span>
          <br>
          <span class="subheading font-weight-bold">
            <v-list dense>
              <v-list-item>
              <v-list-item-content>Fecha Límite de Entrega:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ itemactual.fecha_cierre_format }}
              </v-list-item-content>
              </v-list-item>
              <v-list-item>
              <v-list-item-content>Tiempo Faltante:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ `${tiempoTrascurrido.days} Dias, ${tiempoTrascurrido.hours} Horas, ${tiempoTrascurrido.minutes} Minutos, ${tiempoTrascurrido.seconds} Segundos` }}
              </v-list-item-content>
              </v-list-item>
              <v-list-item>
              <v-list-item-content>Estado:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ itemactual.estado }}
              </v-list-item-content>
              </v-list-item>
            </v-list>
          </span>
          <br>
          <span class="headline justify-center black--text font-weight-bold">
            Documentos Generales
          </span>
          <br>
          <v-data-table
            :headers="headers"
            :items="arrDocs"
            class="elevation-1"
          >
            <template v-slot:item.descargar="{ item }">
              <v-icon
                class="mr-5"
                color="primary"
                @click="downloadFile(item.file_path, item.file_name)"
                v-if="item.file_name != '' && item.file_path != ''"
              >
                archive
              </v-icon>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
     </v-flex>
   </v-container>
</template>
<script>
import { anunciosProcAdquisicion } from '@/services/api'
import axios from 'axios'
export default {
  name: 'anunciosTabRef',
  props: ['itemactual', 'naturalezajuridica'],
  data () {
    return {
      tiempoTrascurrido: [],
      arrDocs: [],
      headers: [
        // { text: 'Id', value: 'id' },
        { text: 'Documento', value: 'descripcion', class: 'subtitle-1 font-weight-bold black--text' },
        // { text: 'Cantidad', value: 'cantidad' },
        // { text: 'Unidad', value: 'unidad' }
        { text: 'Descargar Doc.', value: 'descargar', align: 'center', class: 'subtitle-1 font-weight-bold black--text' }
      ]
    }
  },
  methods: {
    getTimeRemaining (endtime) {
      const total = Date.parse(endtime) - Date.parse(new Date())
      const seconds = Math.floor((total / 1000) % 60)
      const minutes = Math.floor((total / 1000 / 60) % 60)
      const hours = Math.floor((total / (1000 * 60 * 60)) % 24)
      const days = Math.floor(total / (1000 * 60 * 60 * 24))
      this.tiempoTrascurrido = { total, days, hours, minutes, seconds }
    },
    async buscarDocsTermRef () {
      console.log('id_termref ' + this.itemactual.id_termref)
      const { data } = await anunciosProcAdquisicion.traerDocumentosGenerales({
        id_termref: this.itemactual.id_termref
      })
      this.arrDocs = data.termref_adjuntos
    },
    downloadFile (url, nombreArchivo) {
      axios({
        method: 'get',
        url: url,
        responseType: 'arraybuffer'
      })
        .then(response => {
          this.forceFileDownload(response, nombreArchivo)
        })
        .catch(() => console.log('error occured'))
    },
    forceFileDownload (response, nombreArchivo) {
      const url = window.URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', nombreArchivo) // or any other extension
      document.body.appendChild(link)
      link.click()
    }
  },
  watch: {
    itemactual: {
      handler: function (val) {
        if (val) {
          this.getTimeRemaining(this.itemactual.fecha_cierre)
          this.buscarDocsTermRef()
        }
      },
      immediate: true // provides initial (not changed yet) state
    }
  },
  mounted () {
    this.interval = setInterval(() => {
      this.getTimeRemaining(this.itemactual.fecha_cierre)
    }, 1000)
    this.buscarDocsTermRef()
  }
}
</script>
